body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1 {
  margin: auto;
}

.App {
  text-align: center;
  font-family: 'AraJozoor-Regular', Sans-Serif;
  width: 100%;
}

.App-Container {
  display: flex;
  flex-direction: row-reverse;
  flex-flow: row-reverse wrap;
}

.App-DatePicker {
  font-size: smaller;
  text-align: center;
  padding: 10px;
}

.App-block {
  flex: 1 1;
  margin: 10px;
}

.App-header {
  text-align: center;
  font-family: 'AraJozoor-Regular', Sans-Serif;
}

.App-body {
  text-align: center;
  font-family: 'droid-arabic-naskh';
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(7px + 2vmin);
  color: white;
  padding: 40px;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.App-footer {
  flex: 1 1;
  font-size: smaller;
  text-align: center;
  font-family: droid-arabic-naskh;
}

.copyright {
  margin-top: 5px;
  font-family: 'Verdana, Geneva, sans-serif';
  font-size: 12px;
  font-weight: bold;
  direction: ltr;
}

@font-face {
  font-family: 'AraJozoor-Regular';
  src: url(/static/media/AraJozoor-Regular.fa1f99dc.eot);
  src: local('\263A'), url(/static/media/AraJozoor-Regular.b573015b.woff) format('woff'),
    url(/static/media/AraJozoor-Regular.289cda26.ttf) format('truetype'),
    url(/static/media/AraJozoor-Regular.f2b8b184.svg) format('svg');
  font-weight: normal;
  font-style: normal;
}

.Countries {
  margin: 10px;
  min-width: 240px;
  font-family: 'AraJozoor-Regular', Sans-Serif;
}

.Cities {
  margin: 10px;
  font-family: 'AraJozoor-Regular', Sans-Serif;
}


.Error {
  flex: 1 1;
  font-family: 'AraJozoor-Regular', Sans-Serif;
  font-size: smaller;
  margin: 10px;
  white-space: nowrap;
}

.Loader-Spinner {
  flex: 1 1;
  margin: 10px;
}

/* inspired by: https://css-tricks.com/responsive-data-tables/ */
.responsiveTable {
  width: 100%;
  background: white;
  color: black;
  padding: 0 0 5px 0;
}

.responsiveTable td .tdBefore {
  display: none;
}

@media screen and (max-width: 40em) {
  /*
      Force table elements to not behave like tables anymore
      Hide table headers (but not display: none;, for accessibility)
    */

  .responsiveTable table,
  .responsiveTable thead,
  .responsiveTable tbody,
  .responsiveTable th,
  .responsiveTable td,
  .responsiveTable tr {
    display: block;
  }

  .responsiveTable thead tr {
    position: absolute;
    top: -9999px;
    left: -9999px;
    border-bottom: 2px solid #333;
  }

  .responsiveTable tbody tr {
    border: 1px solid #000;
    padding: 0.25em;
  }

  .responsiveTable td.pivoted {
    /* Behave like a "row" */
    border: none !important;
    border-bottom: 1px solid #000;
    position: relative;
    padding-left: 50% !important;
    text-align: center !important;
  }

  .responsiveTable td .tdBefore {
    /* Now like a table header */
    position: absolute;
    display: block;

    /* Top/center values mimic padding */
    left: 1rem;
    width: 45%;
    padding-right: 10px;
    white-space: nowrap;
    text-align: center !important;
    font-weight: 600;
  }
}

.currentDay {
  background: #2e426b !important;
  color: white !important;
}

.differentRow {
  background: #282c34 !important;
  color: white !important;
}

.Container {
  background-color: rgba(173, 172, 146, 0.29);
  padding: 1px 10px;
  min-width: 240px;
  text-shadow: 2px 2px #282c34;
}

.day-time {
  margin: 10px;
}

.img {
  zoom: 5%;
}

.Time-Bloc {
  display: inline-flex;
  align-items: center;
  margin-right: 20px;
}

.title {
  font-family: 'AraJozoor-Regular', Sans-Serif;
}

.Time-Container {
  display: block;
}

.Clock {
  display: inline-flex;
  background-color: #ffffff;
  border-radius: 50%;
}

